body{
  margin: 0;
  font-size: 14px;
  color: #444;
}

.bg-violet{
  background-color: #6200EE;
}

[disabled]:hover{
  cursor: not-allowed;
}

.loading-io{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #fff;
  transition: .5s ease;
  z-index: 2000;
}

.loading-io img{
  max-width: 400px;
  position:absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}