button:focus{
    box-shadow: none !important;
}

.dropdown-menu{
    border-radius: 10px;
}



.rs-page-header{
    height: 250px;
    border-radius: 20px;
    position: relative;
    background-color: rgb(210, 221, 221);
    margin-bottom: 3rem;
}

.rs-page-header h2{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    margin-bottom: 0;
    color: #000;
    font-size: 3rem;
    letter-spacing: 2px;
}

.rs-item-img{
    max-width: 300px;
    width: 100%;
    height: 125px;
    margin: 0 auto 1rem;
    border-radius: 20px;
    object-fit: cover;
    object-position: center;
}


.rs-item-img + div{
    padding: .2rem 1rem;
    text-align: center;
}

.rs-item-title{
    font-weight: 300;
}

.rs-item-desc{
    opacity: .5;
    margin-bottom: 1rem;
}

.rs-item-btn{
    font-weight: 300;
    letter-spacing: 1px;
    padding: 8px 25px;
    border-radius: 20px !important;
    margin: .5rem 0;
}