.login--form{
    padding: 2rem;
}



.rs-input-groups{
    background-color: #f0f0f0;
    margin: 1.5rem 0;
    padding:.5rem 1rem .25rem;
    display: flex;
    align-items: flex-start;
    border-radius: 8px;

}

.rs-input-groups > i{
    font-size: 1.25rem;
    width: 40px;
    margin-top: .5rem;
    color: #0003;
}

.rs-input-groups > div > label{
    opacity: .5;
}

.rs-input-groups > div > input{
    padding-top: 0 !important;
}

.rs-input-groups > div > input:focus{
    border: none;
    box-shadow: none;
    outline: none;
}



.ls-2{
    font-weight: 300;
    font-size: 1rem;
}




/* admin dashboard sidebar */

.rs-sidebar{
    max-width: 220px;
    width:100%;
    padding-top: 10px;
    background-image: linear-gradient(to bottom left, #00d2ff, #928dab);
    height: calc(100vh - 55px);
    position: fixed;
    bottom: 0;
    left: 0;
}

.rs-sidebar ul li a{
    padding:.75rem 1rem;
    
    display: block;
    color: #fff;
    transition: .5s ease;
    text-decoration: none;
}

.rs-sidebar ul li a:hover,
.rs-sidebar ul li a.active{
    background-color: #fff9;
    color: #000;                   
    border-radius: 20px 0 0 20px;
    box-shadow: 0 10px 20px -5px #0002
}

.rs-sidebar ul li a  i{
    margin-right: 1rem;
    font-size: 16px;
    position: relative;
    top: 1px;
}






/* content dashboard */
.rs-content{
    position: absolute;
    top:55px;
    left: 220px;
    width: calc(100% - 220px);
}





/* reservation */
.form-title{
    padding:2rem 2rem 1rem;
    margin-bottom: 0;
}
.reservation-form{
    padding: 0 2rem;
}
.reservation-form  label{
    font-weight: 500;
    margin-bottom: .5rem;
    font-size: 1rem;
}
.reservation-form  textarea.form-control,
.reservation-form  input.form-control,
.reservation-form  select.form-select{
    padding:.75rem 1.25rem;
    border: 2px solid #ccc;
}

.reservation-form  textarea.form-control:focus,
.reservation-form  input.form-control:focus,
.reservation-form  select.form-select:focus{
    border-color: dodgerblue;
    box-shadow: none
}

.rounded-30{
    border-radius: 20px
    ;
}




/* feedback */



/* report */
.table tr{
    border-bottom: 1px solid #3333;
}
.table td{
    padding: 2rem .5rem;
    font-size:.95rem;
    opacity: .75;
}

.w-200{
    max-width: 180px;
    width:100%;
    height: 40px;
}





/* dashboard */

.rs-report-box{
    display: flex;
    align-items:center;
    border-radius: 15px;
    padding: 2rem;
    background-color: #fff;
    box-shadow: 0 10px 20px -5px #0001;
    margin: 1rem auto 1.5rem;
    max-width: 250px;
}

.rs-report-box i{
    font-size:  4.1rem;
    margin-right: 20px;
    color: dodgerblue;
}

.rs-report-box label{
    font-size: 1rem;
}

.rs-report-box h2{
    font-weight: 300;
    font-size: 2.5rem;
    margin-bottom: 0;
}





/* popup css */
.number-arr{
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom:1.5rem;
}

.number-arr span{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    font-weight: bold;
    width: 40px;
    height: 40px;
    border: 1px solid #0003;
    cursor: pointer;
}

.number-arr span:hover,
.number-arr span.active{
    background: tomato;
    border-color: tomato;
    color: #fff;
}

.radius-oval{
    border-radius: 20px;
}

.number-arr ~ textarea{
    font-size: .9rem
}




.file-uploader{
    position: relative;
}

.file-uploader input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
}

.file-uploader label{
    padding: 5px 10px;
    font-weight: bold;
    margin: 10px 0;
}

.modal textarea.form-control{
    padding:.75rem 1.25rem;
    border: 2px solid #ccc;
}

.modal textarea.form-control:focus{
    box-shadow: none;
    border-color: dodgerblue;
}





.restaurant-wrapper{
    padding: 1rem 2rem 2rem;
}

.restaurantBox{
    padding: 2rem;
    max-width: 320px;
    margin-bottom: 2rem;
    width: 100%;
    border:1px solid #4445;
    transition: .5s ease;
    border-radius: 10px;
}



.view-body h4{
    background: #efefef;
    padding: 1rem;
    font-style: italic;
    border-radius: 20px;
    margin-top: 10px;
} 