.bg-gray{
    background-color: #f0f0f0;
}

.rs-feedback-list{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-bottom: 3rem;
}

.rs-feedback-box{
    max-width: 30%;
    width: 100%;
    border: 1px solid #9995;
    padding: 2.4rem 2rem 2rem;
    position: relative;
    margin: 3rem 1rem 1rem;
    border-radius: 20px;
    /* background-color: #efefef; */
    box-shadow: 0 10px 20px -5px #0003;
}

.rs-feedback-box span{
    position: absolute;
    top: -26px;
    left: 20;
    /* transform: translateX(50%); */
    font-size: 3.5rem;
    border-radius: 50%;
    background-color: #fff;
    color: royalblue;
}

.rs-feedback-box i{
    position: relative;
    top: 2px;
}
