.login--form{
    padding: 2rem;
}



.rs-input-groups{
    background-color: #f0f0f0;
    margin: 1.5rem 0;
    padding:.5rem 1rem .25rem;
    display: flex;
    align-items: flex-start;
    border-radius: 8px;

}

.rs-input-groups > i{
    font-size: 1.25rem;
    width: 40px;
    margin-top: .5rem;
    color: #0003;
}

.rs-input-groups > div > label{
    opacity: .5;
}

.rs-input-groups > div > input{
    padding-top: 0 !important;
}

.rs-input-groups > div > input:focus{
    border: none;
    box-shadow: none;
    outline: none;
}



.ls-2{
    font-weight: 300;
    font-size: 1rem;
}





/* searc reservation */
.rs-back-btn{
    font-size: 1.2rem;
    border: 1px solid #000;
    color: #000;
    padding: 5px 10px;
    border-radius: 20px;
}

.rs-back-btn:hover{
    box-shadow: 0 10px 20px -4px #0002;

}

.rs-back-btn i{
    font-size: .9rem;
    position: relative;
    top: -1px;
    margin-right: .5rem;
}

.rs-search-inputs {
    border-radius: 5px;
    max-width: 240px;
    width: 100%;
    padding: 0 .75rem 0 0;
}
.rs-search-inputs input{
    font-size: 14px;
    padding-top: 6px !important;
    padding-bottom: 6px !important;
}
.rs-search-inputs input:focus{
    outline: none;
    box-shadow: none !important;
}









#rs-reservation-list {}

#rs-reservation-list td span{
    border: 1px solid #ddd;
    box-shadow: 9 10px 20px -5px #0003;
    border-radius: 20px;
    margin:0 6px 6px 0;
    padding: 3px 9px;
    display: inline-block;
}

#rs-reservation-list tbody tr{
    box-shadow: 0 0 15px -5px #0002;
    border-bottom: 1px solid #0009;
}

#rs-reservation-list tbody td{
    padding: 20px 10px;
    font-size: 13px;
}

select:focus{
    box-shadow: none !important;
}






/* dashboard */

.rs-report-box{
    display: flex;
    align-items:center;
    border-radius: 15px;
    padding: 2rem;
    background-color: #fff;
    box-shadow: 0 10px 20px -5px #0002;
    border:1px solid #0002 ;
    margin: 1rem auto 1.5rem;
    max-width: 300px;
}

.rs-report-box i{
    font-size:  4.1rem;
    margin-right: 30px;
    color: dodgerblue;
}

.rs-report-box label{
    font-size: 1rem;
}

.rs-report-box h2{
    font-weight: 300;
    font-size: 2.5rem;
    margin-bottom: 0;
}




.fs-5{
    font-size: 4rem !important;
}

.filter-select{
    border-radius: 5px;
    border:1px solid #333;
    padding: 6px 10px;
    margin-left:10px;

}

table tr:last-child{
    border-bottom: none !important;
}

.filter-select select:focus{
    outline: none;
}